"use client"

import Link from "next/link"
import { ChevronUp, Menu, X } from "lucide-react"
import { useState, useEffect } from "react"
import { whitepaperContent } from "./data"

export default function WhitepaperPage() {
  const [scrollToTop, setScrollToTop] = useState(false)
  const [activeSection, setActiveSection] = useState("")
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false)

  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > 300) {
        setScrollToTop(true)
      } else {
        setScrollToTop(false)
      }

      // Update active section based on scroll position
      const sections = document.querySelectorAll("section[id]")
      let current = ""
      sections.forEach((section) => {
        const sectionTop = (section as HTMLElement).offsetTop
        if (window.scrollY >= sectionTop - 150) {
          current = section.getAttribute("id") || ""
        }
      })
      setActiveSection(current)
    }

    window.addEventListener("scroll", handleScroll)
    return () => window.removeEventListener("scroll", handleScroll)
  }, [])

  const handleScrollToTop = () => {
    window.scrollTo({ top: 0, behavior: "smooth" })
  }

  const scrollToSection = (id: string) => {
    const element = document.getElementById(id)
    if (element) {
      const offset = 100 // Adjust for fixed header
      const bodyRect = document.body.getBoundingClientRect().top
      const elementRect = element.getBoundingClientRect().top
      const elementPosition = elementRect - bodyRect
      const offsetPosition = elementPosition - offset

      window.scrollTo({
        top: offsetPosition,
        behavior: "smooth",
      })
      setIsMobileMenuOpen(false)
    }
  }

  return (
    <div className="w-full min-h-screen bg-background text-foreground selection:bg-primary/20">
      {/* Header */}
      <header className="fixed top-0 w-full z-50 bg-background/80 backdrop-blur-md border-b border-border/50">
        <nav className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4 flex items-center justify-between">
          <Link
            href="/"
            className="flex items-center gap-2 text-xl font-bold text-white hover:opacity-80 transition-opacity"
          >
            SIGNigeria
          </Link>

          <div className="hidden md:flex items-center gap-6">
            <Link href="/" className="text-sm text-foreground/70 hover:text-accent transition-colors">
              Back to Home
            </Link>
            <button className="px-4 py-2 rounded-full bg-primary text-white text-sm font-medium hover:bg-primary/90 transition-colors">
              Connect Wallet
            </button>
          </div>

          {/* Mobile Menu Button */}
          <button
            className="md:hidden p-2 text-foreground/70 hover:text-white"
            onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
          >
            {isMobileMenuOpen ? <X /> : <Menu />}
          </button>
        </nav>
      </header>

      {/* Mobile Navigation Overlay */}
      {isMobileMenuOpen && (
        <div className="fixed inset-0 z-40 bg-background/95 backdrop-blur-sm pt-24 px-6 md:hidden overflow-y-auto">
          <div className="space-y-4">
            <div className="pb-4 border-b border-border/50">
              <h3 className="text-sm font-semibold text-foreground/50 uppercase tracking-wider mb-4">Contents</h3>
              <div className="space-y-2">
                {whitepaperContent.map((section) => (
                  <button
                    key={section.id}
                    onClick={() => scrollToSection(section.id)}
                    className={`block w-full text-left py-2 text-sm transition-colors ${activeSection === section.id ? "text-primary font-medium" : "text-foreground/70"
                      }`}
                  >
                    {section.title}
                  </button>
                ))}
              </div>
            </div>
            <Link
              href="/"
              className="block py-3 text-foreground/70 hover:text-white border-b border-border/50"
              onClick={() => setIsMobileMenuOpen(false)}
            >
              Back to Home
            </Link>
          </div>
        </div>
      )}

      <main className="pt-32 pb-20 px-4 sm:px-6 lg:px-8 max-w-7xl mx-auto">
        <div className="grid grid-cols-1 lg:grid-cols-12 gap-12">
          {/* Sidebar Navigation (Desktop) */}
          <aside className="hidden lg:block lg:col-span-3 relative">
            <div className="sticky top-32 space-y-8">
              <div>
                <h3 className="text-sm font-semibold text-foreground/50 uppercase tracking-wider mb-4">Contents</h3>
                <nav className="space-y-1 border-l border-border/50">
                  {whitepaperContent.map((section) => (
                    <button
                      key={section.id}
                      onClick={() => scrollToSection(section.id)}
                      className={`block w-full text-left pl-4 py-2 text-sm border-l-2 transition-all duration-200 ${activeSection === section.id
                          ? "border-primary text-primary font-medium bg-primary/5"
                          : "border-transparent text-foreground/60 hover:text-foreground hover:border-foreground/20"
                        }`}
                    >
                      {section.title}
                    </button>
                  ))}
                </nav>
              </div>

              <div className="p-6 rounded-xl bg-card/30 border border-border/50">
                <h4 className="font-semibold text-white mb-2">Join the Community</h4>
                <p className="text-xs text-foreground/60 mb-4">Be part of the Nigeria On-Chain movement.</p>
                <button className="w-full py-2 rounded-lg bg-accent/10 text-accent border border-accent/20 hover:bg-accent/20 transition-colors text-sm font-medium">
                  Join Discord
                </button>
              </div>
            </div>
          </aside>

          {/* Content Area */}
          <div className="lg:col-span-9 space-y-16">
            {/* Hero Title */}
            <div className="space-y-6 border-b border-border/50 pb-12">
              <div className="inline-flex items-center gap-2 px-3 py-1 rounded-full bg-primary/10 text-primary text-xs font-medium border border-primary/20">
                <span>Draft 1.0</span>
                <span className="w-1 h-1 rounded-full bg-primary"></span>
                <span>Comprehensive</span>
              </div>
              <h1 className="text-4xl md:text-6xl font-bold text-white leading-tight">
                SIGNigeria
                <span className="block text-transparent bg-clip-text bg-gradient-to-r from-primary to-accent mt-2">
                  Whitepaper
                </span>
              </h1>
              <p className="text-xl text-foreground/70 max-w-2xl leading-relaxed">
                Bringing Nigeria On-Chain â€¢ Building Web3 Communities â€¢ Empowering the Future
              </p>
            </div>

            {/* Dynamic Content Rendering */}
            <div className="space-y-24">
              {whitepaperContent.map((section) => (
                <section key={section.id} id={section.id} className="scroll-mt-32 space-y-8">
                  <h2 className="text-3xl font-bold text-white flex items-center gap-3">
                    {section.title}
                    <div className="h-px flex-1 bg-border/50 ml-4"></div>
                  </h2>

                  {section.content && (
                    <p className="text-lg text-foreground/80 leading-relaxed text-justify">
                      {section.content}
                    </p>
                  )}

                  {section.subsections && (
                    <div className="space-y-12">
                      {section.subsections.map((sub, idx) => (
                        <div key={idx} className="space-y-4">
                          {sub.title && (
                            <h3 className="text-xl font-semibold text-accent">
                              {sub.title}
                            </h3>
                          )}

                          {sub.content && (
                            <p className="text-foreground/80 leading-relaxed">
                              {sub.content}
                            </p>
                          )}

                          {sub.items && (
                            <div className="grid gap-6 mt-6">
                              {sub.items.map((item, i) => (
                                <div key={i} className="p-6 rounded-xl bg-card/40 border border-border/50 hover:border-primary/30 transition-colors">
                                  <h4 className="text-lg font-medium text-white mb-2">{item.title}</h4>
                                  {item.desc && <p className="text-foreground/70 text-sm">{item.desc}</p>}
                                  {item.features && (
                                    <ul className="mt-3 space-y-2">
                                      {item.features.map((feature, f) => (
                                        <li key={f} className="flex items-start gap-2 text-sm text-foreground/70">
                                          <span className="text-primary mt-1">â€¢</span>
                                          <span>{feature}</span>
                                        </li>
                                      ))}
                                    </ul>
                                  )}
                                </div>
                              ))}
                            </div>
                          )}
                        </div>
                      ))}
                    </div>
                  )}

                  {section.list && (
                    <ul className="grid gap-4 sm:grid-cols-2">
                      {section.list.map((item, idx) => (
                        <li key={idx} className="flex items-start gap-3 p-4 rounded-lg bg-primary/5 border border-primary/10">
                          <span className="text-primary font-bold">âœ“</span>
                          <span className="text-foreground/80">{item}</span>
                        </li>
                      ))}
                    </ul>
                  )}
                </section>
              ))}
            </div>
          </div>
        </div>
      </main>

      {/* Footer */}
      <footer className="border-t border-border/30 bg-card/30 py-12 px-4 sm:px-6 lg:px-8 mt-20">
        <div className="max-w-6xl mx-auto text-center space-y-4">
          <p className="text-foreground/60 text-sm">&copy; 2025 SIGNigeria. All rights reserved.</p>
          <div className="flex justify-center gap-6 text-sm text-foreground/50">
            <Link href="#" className="hover:text-primary transition-colors">Privacy Policy</Link>
            <Link href="#" className="hover:text-primary transition-colors">Terms of Service</Link>
            <Link href="#" className="hover:text-primary transition-colors">Contact</Link>
          </div>
        </div>
      </footer>

      {/* Scroll to Top Button */}
      <button
        onClick={handleScrollToTop}
        className={`fixed bottom-8 right-8 p-3 rounded-full bg-primary text-white hover:bg-primary/90 transition-all duration-300 shadow-lg shadow-primary/20 z-50 ${scrollToTop ? "opacity-100 translate-y-0" : "opacity-0 translate-y-10 pointer-events-none"
          }`}
      >
        <ChevronUp className="w-6 h-6" />
      </button>
    </div>
  )
}
